<template>
  	<div id="user">
  		<section class="application container">
            <div class="d-flex align-items-center bread border-bottom">
            	您的位置： 账号管理
            	<router-link to="/userdetails" class="btn_1">
            		<el-button type="primary" size="mini" round>添加</el-button>
            	</router-link>
            </div>
            
            <table class="table" style="min-height: 335px;">
            	<table class="table">
				  	<thead class="table-primary">
					    <tr>
					      	<th>姓名</th>
					      	<th>账号</th>
					      	<th>类型</th>
					      	<th>最后登录时间</th>
					      	<th scope="col" class="text-right">操作</th>
					    </tr>
				  	</thead>
				  	<tbody>
				    	<tr v-for="x in user_obj.DataList">
				      		<td scope="row">{{x.Name}}</td>
				      		<td>{{x.Code}}</td>
				      		<td>{{type[x.Type]}}</td>
				      		<td>{{x.LastLoginTime}}</td>
				      		<td class="text-right">
				      			<router-link :to="'/userdetails/'+x.Id">
				      	 			<el-button type="primary" icon="el-icon-edit" size="mini" circle></el-button>
				      	 		</router-link>
				      	 		<el-button type="danger" @click="delete_1(x)" icon="el-icon-delete" size="mini" circle></el-button>
				      		</td>
			    		</tr>
				  	</tbody>
				</table>
            </table>
            
            <div class="block text-right">
			    <el-pagination 
			     	@current-change="handleCurrentChange" 
			     	:current-page.sync="currentPage4" 
			     	:page-size="per_page" 
			     	layout="prev, pager, next, jumper" 
			     	:total="user_obj.MaxCount">
    			</el-pagination>
			</div>
            
        </section>
	
  	</div>
</template>

<script>
	import {http,paging} from '@/assets/fc';
	export default {
	  	name: '',
	  	data(){
	  		return{
	  			Prompt:'',
	  			page:1,			//页数
	  			per_page:5,		//每页显示条数
	  			user_obj:{	
	  				DataList:[],
	  				MaxCount:1,
	  				MaxPage:1
	  			},
	  			pagind:{		//分页
	  				shang:'',
	  				list:[],
	  				xia:''
	  			},
				currentPage4: 4,
				type:['管理员','企业','机构']   
	  		}
	  	},
	  	methods:{
	  		//获取用户
	  		get_user(){
	  			http(this,'get','/user?page='+this.page+'&per_page='+this.per_page+'&user_role=&user_name=','',this.user_return)
	  		},
	  		user_return(x){
	  			console.log('获取用户',x);
	  			this.user_obj=x.Memory;
	  		},
		    handleCurrentChange(x) {
//		        console.log(`当前页: ${x}`);
				console.log(x)
				this.page=x;
				this.get_user()
		    },
		    //删除
		    delete_1(x){
		    	console.log(x);
		    	this.$confirm('此操作将永久删除, 是否继续?', '提示', {
		          	confirmButtonText: '确定',
		          	cancelButtonText: '取消',
		          	type: 'warning'
		        }).then(() => {
		          	http(this,'delete','/user',{'Id':x.Id},this.delete_retun)
		        }).catch(() => {
		          	          
		        });
		    },
		    delete_retun(x){
		    	console.log(x);
		    	if(x.Result){
		    		this.get_user()		//获取
		    	}else{
		    		this.$message({
			          	showClose: true,
			          	message: x.Message,
			          	type: 'error'
			        });
		    	}
		    }
	  	},
	    created: function () {
	        this.get_user()		//获取
	    },
	    mounted: function () {

	    },
	}
</script>

<style>
	#user .btn_1{
		position: absolute;
		right: 0px;
	}
	
</style>



